/*
* On the web the hero section is typically a prominent image, slider,
* text or similar element that has pride of place at the top of your
* homepage layout and possibly subsequent pages. It is front and
* center and in your face.
*/

import React from "react";
import {Jumbotron, Container, Row, Col} from 'react-bootstrap';

function Hero(props) {
    return(
        <Jumbotron className="bg-transparent jumbotron-fluid p-0">
            <Container fluid={true}>
                <Row className="justify-content-center py-5">
                    <Col md={8} sm={12}>
                        { props.title && <h1 className="display-1 font-weight-bolder">{props.title}</h1> }
                        { props.subTitle && <h3 className="display-4 font-weight-light">{props.subTitle}</h3> }
                        { props.text && <h3 className="lead font-weight-light">{props.text}</h3> }
                    </Col>
                </Row>
            </Container>
        </Jumbotron>
    );
}

export default Hero;